/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'
import GLightbox from 'glightbox'
import Image from 'react-shimmer'
import Spinner from "react-spinners/RingLoader";

const lightbox = GLightbox({
    touchNavigation: true,
    loop: true,
    autoplayVideos: true,
    onOpen: () => { },
    beforeSlideLoad: (slide, data) => { }
});

function LocalPicture(app, feature, index, openLb) {
    const aosList = [
        "slide-up",
        "flip-left",
        "zoom-in",
        "flip-right"
    ]
    const shadowCss = feature.shadow ? css`
            border-radius: 5px;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            overflow: hidden;

            width: 100%;
            height: auto;
        ` : css``

    const aos = aosList[index % aosList.length]

    return (
        <div css={css`
            
        `} data-aos={aos}>
            <div>
                <a
                    href='#feature'
                    className="glightbox"
                    onClick={openLb}
                >
                    <Image
                        css={shadowCss}
                        src={require(`../apps/${app.slug}/${feature.image}`)}
                        alt={feature.image}
                        fallback={
                            <Spinner
                                size={30}
                                color={"#27CBC0"}
                                loading={true}
                            />
                        }
                    />
                </a>
            </div>
        </div>
    )
}

const RemotePicture = (feature) => {
    return (
        <div css={css`
            align: center;
        `}>
            <Image css={css`
                border-radius: 5px;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                overflow: hidden;
                max-width: 98%;
            `}
                src={feature.remote_image}
                alt={feature.remote_image}
                fallback={
                    <Spinner
                        size={30}
                        color={"#27CBC0"}
                        loading={true}
                    />
                }
            />
        </div>
    )
}

export default function Features(props) {
    const app = props.app
    const features = app.features

    const lbElements = features
        .filter((feature) => { return feature.image !== undefined })
        .map((feature) => {
            return {
                'href': require(`../apps/${app.slug}/${feature.image}`),
                'type': 'image',
                'title': feature.title,
                'description': feature.texts[0],
            }
        })

    const openLb = () => {
        lightbox.setElements(lbElements)
        lightbox.open()
    }

    let list = features.map((feature, index) => {
        const texts = feature.texts.map((text) => {
            return (
                <span css={css`
                    diplay: inline-block;
                    margin: 6px;
                    color: #3E3E3E;
                    font-weight: semi-bold;
                    font-size: 16px;
                `}
                    key={text}
                >
                    {text}
                </span>
            )
        })

        const Picture = () => {
            if (feature.remote_image !== undefined) {
                return RemotePicture(feature)
            } else {
                return LocalPicture(app, feature, index, openLb)
            }
        }

        const Content = () => (
            <div css={css`
                display: grid;
            `} data-aoes="fade-up">
                <div css={css`
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                `}>
                    <i className='material-icons-round'>{feature.titleIcon}</i>
                    <span css={css`
                        font-weight: bold;
                        font-size: 24px;
                        margin-left: 5px;
                    `}>{feature.title}</span>
                </div>
                {texts}
            </div>
        )

        const Left = (index % 2 == 0) ? <Picture /> : <Content />
        const Right = (index % 2 == 0) ? <Content /> : <Picture />

        return (
            <div css={css`
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-gap: 8vw;
                align-items: center;
                margin-top: 100px;
                margin-bottom: 50px;
                min-height: 50vh;
                padding: 0 5vw;

                @media (max-width: 500px) {
                    grid-template-columns: 1fr; 
                }
            `}
                key={index}
            >
                {Left}
                {Right}
            </div>
        )
    })

    return (
        <div css={css`
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
        `}>
            <div css={css`
                margin: 0 auto;
            `}>
                <p css={css`
                    font-style: bold;
                    font-weight: 900;
                    font-size: 2em;
                    margin-bottom: 100px;
                `}>Features</p>
            </div>
            {list}
        </div>
    )
}